import axios from 'axios'
import qs from 'qs'

const homeworkUrl = '/api/homeworks'
const assignmentUrl = '/api/assignments'

class HomeworkService {
    static async GiveHomework(homeworkBody) {
        const response = await axios.post(homeworkUrl, { homeworkBody })
        return response.data
    }

    static async GetAssignments(homeworkId) {
        const response = await axios.get(`${homeworkUrl}/${homeworkId}` + '/assignments')
        return response.data
    }

    static async GetHomework(homeworkId) {
        const response = await axios.get(`${homeworkUrl}/${homeworkId}`)
        return response.data
    }

    static async DeleteHomework(homeworkId) {
        const response = await axios.delete(`${homeworkUrl}/${homeworkId}`)
        return response.data
    }

    static async ReviewAssignment(assignment) {
        const response = await axios.patch(`${assignmentUrl}/review/${assignment.id}`, assignment)
        return response.data
    }

    static async GetAssignment(assignmentId, attributes) {
        const response = await axios.get(`${assignmentUrl}/${assignmentId}`, {
            params: {
                attributes
            },
            paramsSerializer: params => {
                return qs.stringify(params)
            }
        })
        return response.data
    }

    static async GetHomeworkCourse(homeworkId) {
        const response = await axios.get(`${homeworkUrl}/${homeworkId}/course`)
        return response.data
    }

    static async GetAssignmentsOfEnrolled(enrolledId) {
        const response = await axios.get(`/api/enrolled/${enrolledId}/assignments`);
        return response.data;
    }
}

export default HomeworkService;