<template>
  <div>
    <v-row class="mb-3">
      <v-card class="mx-3" raised outlined>
        <v-card-title>
          <v-list>
            <v-list-item>
              Offer: {{ course.name }} - {{ offer.code }}
            </v-list-item>
            <v-list-item> Average: {{ offer.average }} </v-list-item>
            <v-list-item> Student Count: {{ enrolleds.length }} </v-list-item>
            <v-list-item> Created At: {{ offer.createdAt }} </v-list-item>
          </v-list>
        </v-card-title>
      </v-card>
      <v-btn
        class="mx-3"
        color="primary"
        large
        @click.stop="showHomeworkForm = true"
      >
        Yeni Ödev
      </v-btn>
      <v-btn
        class="mx-3"
        color="primary"
        large
        @click.stop="openStudentsDialog()"
      >
        Öğrenciler
      </v-btn>
      <v-btn class="mx-3" color="primary" large @click="calculateOfferAverage">
        Ortalamayı Hesapla
      </v-btn>

      <HomeworkForm v-model="showHomeworkForm" :offerId="offerId" />
      <LoadingDialog v-model="showLoadingDialog" />

      <v-spacer></v-spacer>
    </v-row>

    <v-divider></v-divider>

    <v-card raised outlined class="my-3">
      <v-expansion-panels>
        <v-expansion-panel>
          <v-expansion-panel-header>
            <h1>Homeworks</h1>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <v-row>
              <v-col
                cols="12"
                sm="6"
                md="4"
                v-for="(homework, index) in homeworks"
                :key="index"
                @click="goToHomework(homework)"
              >
                <v-card raised outlined>
                  <v-card-title>
                    <v-icon class="mr-2">mdi-book</v-icon>
                    {{ homework.name }}
                    <v-btn icon @click="deleteHomework($event, homework.id)">
                      <v-icon>mdi-delete</v-icon>
                    </v-btn>
                  </v-card-title>
                  <v-card-text> Average: {{ homework.average }} </v-card-text>
                </v-card>
              </v-col>
            </v-row>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </v-card>

    <v-divider></v-divider>

    <v-row>
      <v-col cols="2">
        <v-btn color="success" @click="openBulkMarkDialog">
          <v-icon left>mdi-pen-plus</v-icon>
          Add Mark
        </v-btn>
      </v-col>
      <v-col cols="2">
        <v-btn color="success" @click="save">
          <v-icon left>mdi-content-save</v-icon>
          Save
        </v-btn>
      </v-col>
      <v-divider></v-divider>
    </v-row>

    <v-divider></v-divider>

    <v-row>
      <v-col cols="6" v-for="(enrolled, index) in enrolleds" :key="index">
        <v-card raised outlined>
          <v-card-text>
            <v-row>
              <v-col cols="4" class="body-1">
                <v-card raised outlined height="100%">
                  <v-card-text>
                    <v-list>
                      <v-list-item>
                        <span>
                          Name:
                          <b>
                            {{ enrolled.Student.firstName + " " }}
                            <span v-if="enrolled.Student.middleName">{{
                              enrolled.Student.middleName + " "
                            }}</span>
                            {{ enrolled.Student.lastName }}
                          </b>
                        </span>
                      </v-list-item>
                      <v-list-item v-if="enrolleds.length == enrolleds.length">
                        Average:
                        <b> {{ " " + enrolleds[index]["result"] }}</b>
                      </v-list-item>
                      <v-spacer vertical></v-spacer>
                      <v-list-item>
                        <v-btn icon color="primary" @click="addNewMark(index)">
                          <v-icon>mdi-pen-plus</v-icon>
                        </v-btn>
                        <v-btn
                          icon
                          color="primary"
                          class="ml-5"
                          @click="
                            addMarkFromAssignments(enrolleds[index].id, index)
                          "
                        >
                          <v-icon>mdi-book-plus</v-icon>
                        </v-btn>
                      </v-list-item>
                    </v-list>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col
                cols="4"
                v-for="(mark, index) in enrolled.marks"
                :key="index"
              >
                <v-card raised outlined>
                  <v-card-text>
                    <v-text-field
                      prepend-icon="mdi-rename-box"
                      dense
                      outlined
                      placeholder="Name"
                      v-model="mark.name"
                    >
                    </v-text-field>
                    <v-text-field
                      prepend-icon="mdi-numeric"
                      dense
                      outlined
                      type="number"
                      max="100"
                      min="0"
                      placeholder="Points"
                      v-model="mark.points"
                    >
                    </v-text-field>
                    <v-text-field
                      prepend-icon="mdi-brightness-percent"
                      dense
                      placeholder="Percentage"
                      type="number"
                      max="100"
                      min="0"
                      v-model="mark.percentage"
                      outlined
                    >
                    </v-text-field>
                  </v-card-text>
                </v-card>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-dialog v-model="showUploadDialog" max-width="40vw">
      <v-card>
        <v-card-title>
          <v-file-input
            v-model="file"
            show-size
            label="File input"
          ></v-file-input>
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green" @click="uploadFile">
            Create
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="showStudentsDialog" scrollable max-width="60vw">
      <v-card>
        <v-card-title>
          <v-btn
            icon
            color="error"
            @click="unenrollStudent()"
            :disabled="selectedStudents.length < 1"
          >
            <v-icon>mdi-delete</v-icon>
          </v-btn>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="studentSearch"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          >
          </v-text-field>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="enrolleds"
          :search="studentSearch"
          show-select
          v-model="selectedStudents"
        >
          <template v-slot:item.action="{ item }">
            <v-icon @click="goToStudentProfile(item)">
              mdi-account-search
            </v-icon>
          </template>
        </v-data-table>
      </v-card>
    </v-dialog>

    <v-dialog v-model="showBulkMarkDialog" scrollable max-width="25vw">
      <v-card>
        <v-card-title>
          <v-list>
            <v-list-item>
              <v-switch
                color="primary"
                label="Use Homework"
                v-model="useHomework"
                @change="changeUseHomework"
              >
              </v-switch>
            </v-list-item>
            <v-list-item>
              <v-text-field
                prepend-inner-icon="mdi-rename-box"
                outlined
                dense
                label="Name"
                v-model="bulkMarkName"
              >
              </v-text-field>
            </v-list-item>
            <v-list-item>
              <v-text-field
                v-if="!useHomework"
                prepend-inner-icon="mdi-numeric"
                dense
                outlined
                type="number"
                max="100"
                min="0"
                label="Points"
                v-model="bulkMarkPoints"
              >
              </v-text-field>
            </v-list-item>
            <v-list-item>
              <v-text-field
                prepend-inner-icon="mdi-brightness-percent"
                dense
                label="Percentage"
                type="number"
                max="100"
                min="0"
                v-model="bulkMarkPercentage"
                outlined
              >
              </v-text-field>
            </v-list-item>
          </v-list>
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="createBulkMark">
            Create
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-snackbar
      v-model="snackbar"
      bottom
      :color="snackbarColor"
      :timeout="snackbarTimeout"
    >
      {{ snackbarText }}
      <v-btn text @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import OfferService from "../services/OfferService";
import CourseService from "../services/CourseService";

import LoadingDialog from "../components/LoadingDialog";
import HomeworkForm from "../components/HomeworkForm";
import HomeworkService from "../services/HomeworkService";
import StudentService from "../services/StudentService";

export default {
  components: {
    HomeworkForm,
    LoadingDialog
  },
  data() {
    return {
      showUploadDialog: false,
      showStudentsDialog: false,
      showLoadingDialog: true,
      offer: {},
      average: 0,
      file: {},
      course: {},
      homeworks: [],
      enrolleds: [],
      selectedStudents: [],
      studentSearch: "",
      offerId: Number,
      showHomeworkForm: false,
      showBulkMarkDialog: false,
      bulkMarkName: "Exam",
      bulkMarkPoints: 0,
      bulkMarkPercentage: "100",
      headers: [
        {
          text: "First Name",
          value: "firstName"
        },
        {
          text: "Middle Name",
          value: "middleName"
        },
        {
          text: "Last Name",
          value: "lastName"
        },
        { text: "Average", value: "average" },
        { text: "Actions", value: "action", sortable: false }
      ],
      snackbar: false,
      snackbarText: "Snackbar",
      snackbarColor: "primary",
      snackbarTimeout: 3000,
      useHomework: false
    };
  },
  created() {
    this.offerId = this.$route.params.offerId;
    this.getStudents();
    OfferService.GetOffer(this.offerId).then(offer => {
      this.offer = offer;
      OfferService.GetHomeworks(this.offerId).then(homeworks => {
        this.homeworks = homeworks;
      });
      CourseService.GetCourseById(offer.courseId).then(course => {
        this.course = course;
      });
    });
  },
  methods: {
    async getStudents() {
      OfferService.GetEnrolleds(this.offerId).then(enrolleds => {
        this.enrolleds = enrolleds;
      });
    },
    goToHomework(homework) {
      this.$router.push({
        name: "Assignments",
        params: { homeworkId: homework.id, homework: homework }
      });
    },
    deleteHomework(event, homeworkId) {
      event.preventDefault();
      HomeworkService.DeleteHomework(homeworkId);
    },
    unenrollStudent(offerId) {
      let studentIds = [];
      for (let i = 0; i < this.selectedStudents.length; i++) {
        const el = this.selectedStudents[i];
        studentIds.push(el.id);
      }

      OfferService.UnenrollStudents(offerId, studentIds).then(() => {
        // this.$router.go()
      });
    },
    goToStudentProfile(clickedRow) {
      this.$router.push({
        name: "ViewStudent",
        params: { studentId: clickedRow.id }
      });
    },
    openStudentsDialog() {
      this.showStudentsDialog = true;
    },
    openBulkMarkDialog() {
      this.showBulkMarkDialog = true;
    },
    createBulkMark() {
      let markPromises = [];

      for (let index = 0; index < this.enrolleds.length; index++) {
        this.addNewMark(index, false);

        if (this.useHomework) {
          const enrolled = this.enrolleds[index];
          this.showLoadingDialog = true;
          markPromises.push(this.addMarkFromAssignments(enrolled.id, index));
        }
      }

      Promise.all(markPromises).then(() => {
        this.showLoadingDialog = false;
      });
      this.showBulkMarkDialog = false;
    },
    addNewMark(index, special = true) {
      let mark = {
        special,
        name: this.bulkMarkName,
        points: this.bulkMarkPoints,
        percentage: this.bulkMarkPercentage
      };
      if (this.useHomework) {
        mark.points = 0;
      }
      this.enrolleds[index].marks.push(mark);
    },
    async addMarkFromAssignments(enrolledId, index) {
      return OfferService.GetEnrolledAssignmentsMarks(enrolledId).then(
        marks => {
          console.log(marks);
          
          if (marks.length == 0) {
            return;
          }
          let markLength = this.enrolleds[index].marks.length;
          let temp = this.enrolleds[index].marks[markLength - 1];
          temp.points = StudentService.CalculateAssignmentsAverage(marks);
          this.$set(this.enrolleds[index].marks, markLength - 1, temp);
        }
      );
    },
    save() {
      for (let index = 0; index < this.enrolleds.length; index++) {
        const student = this.enrolleds[index];
        this.enrolleds.marks = student.marks;
        StudentService.UpdateEnrolled(this.enrolleds[index]).then(() => {
          this.snackbar = true;
          this.snackbarColor = "success";
          this.snackbarText = "Updated";
        });
      }
    },
    uploadFile() {
      OfferService.UploadFile(this.offerId, this.file).then(success => {
        this.snackbar = true;
        if (success) {
          this.snackbarColor = "success";
          this.snackbarText = "Uploaded.";
        } else {
          this.snackbarColor = "error";
          this.snackbarText = "Error uploading!";
        }
      });
    },
    changeUseHomework(useHomework) {
      if (useHomework) {
        this.bulkMarkName = "Homework";
      } else {
        this.bulkMarkName = "Exam";
      }
    },
    calculateOfferAverage() {
      let sum = 0;
      const length = this.enrolleds.length;
      for (let i = 0; i < length; i++) {
        sum += this.enrolleds[i].result;
      }
      const average = sum / length;
      this.average = average;
    }
  }
};
</script>

<style>
</style>